import {
  PartialShippingAddress,
  ShippingEstimateShippingMethod,
  ShippingMethod,
} from '@/checkout/data/models';

type TransformShippingEstimatePartialAddressInput = {
  country_id: string;
  postcode?: string;
  region_id?: number;
  region?: string;
};

export const transformShippingEstimatePartialAddress = (
  data: TransformShippingEstimatePartialAddressInput
): PartialShippingAddress => {
  return {
    countryCode: data.country_id,
    postCode: data.postcode || '',
    ...(data.region_id
      ? { regionId: Number(data.region_id) }
      : { ...(data.region ? { region: data.region } : {}) }),
  };
};

export const transformShippingEstimateShippingMethod = (
  data: ShippingMethod
): ShippingEstimateShippingMethod => {
  return {
    carrierCode: data.carrier.code || '',
    methodCode: data.code || '',
    amount: data.amount,
    amountExclTax: data.amountExclTax,
    amountInclTax: data.amountInclTax,
  };
};
